$normal-box-size: 70px;
$normal-font-size: 32px;

$mobile-box-size: 50px;
$mobile-font-size: 28px;

.form {
    margin: auto;
    display: table;
}

.main-box {
    display: table;
    margin: 50px auto;

    .attempt-box {
        display: flex;
        margin: 15px;

        .letter-box {
            margin-right: 10px;
            height: $normal-box-size;
            width: $normal-box-size;
            border: solid 3px #888888;
            display: flex;
            border-radius: 5px;
    
            span {
                color: #333333;
                font-weight: 700;
                font-size: $normal-font-size;
                margin: auto;
            }
    
            &.exist {
                background-color: lightyellow;
            }
    
            &.right {
                background-color: lightgreen;
            }
    
            &.wrong {
                background-color: gray;
            }
        }        
    }

    @media (max-width: 790px) {
        .attempt-box {    
            margin: 10px;

            .letter-box {
                height: $mobile-box-size;
                width: $mobile-box-size;
            }
        }
    }
}